<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <div id="modelAddProduct" role="dialog" class="modal my-popups right fade" v-cloak>

        <div role="document" class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header mheader-user">
                    <button type="button" data-dismiss="modal" class="close" v-on:click="close()">
                        <em aria-hidden="true" class="fa fa-times"></em>
                    </button>

                    <h4 id="conditionalTitle" class="modal-title" v-if="popupCondition == 'setup'">{{ $t("Setup") }}
                    </h4>
                    <h4 id="conditionalTitle" class="modal-title" v-else>{{ $t("AddItem") }}</h4>

                    <span v-if="popupCondition != 'setup'" class="user-guide">
                        <a class="ancuserguide" data-toggle="collapse" href="#collapseReplyUG" role="button" aria-expanded="false" aria-controls="collapseExample"> User Guide</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('UserGuideAddProduct')"></div>
                            </div>
                        </div>
                    </span>
                </div>
                <div class="modal-body">
                    <div class="left-menu-tab new_tab" v-if="popupCondition == 'setup'">
                        <div class="col-md-12 pb-0">
                            <div class="row">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                    <div class="row">
                                        <div class="col-8 pr-0 font-weight-medium">{{ $t('CompleteYourProfile') }}
                                        </div>
                                        <div class="col-4 pl-0 text-right"><small><i class="fa fa-check-circle pr-1" v-bind:class="{ 'text-success': SetupProgresText == 'Completed', 'text-muted': SetupProgresText !== 'Completed' }"></i>{{ SetupProgresText
                                                    }}</small>
                                        </div>
                                    </div>
                                    <div class="progress smaller my-2">
                                        <div s class="progress-bar progress-bar-primary progress-bar px-0 text-center memoryused" id="divConsumendStorage" :class="`${progressBarColor}`" :style="`width: ${SetupProgress}%; font-weight:500; font-size:14px`" :title="SetupProgress">{{ SetupProgress }}%
                                        </div>
                                    </div>
                                    <div class="level_info my-2 d-block float-left w-100" style="height:auto;">
                                        <ul>
                                            <li>
                                                <b id="divConsumendStorage"><span>{{ $t('Progress') }} {{ CurrentTab
                                                            }}
                                                        of
                                                        3</span></b>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);">
                            <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">
                                <li class="nav-item FourthScreen" @click="SkipTab(1)"><a class="nav-link left-menu" :class="{ 'active': step == 1 }" style="cursor: pointer;"><i class="fa fa-random pr-2 text-muted" aria-hidden="true"></i><span class="font-14">{{ $t('SetUpLocation') }}</span></a></li>
                                <li class="nav-item SecondScreen" @click="SkipTab(2)"><a class="nav-link left-menu" :class="{ 'active': step == 2 }" style="cursor: pointer;"><i class="fa fa fa-cog pr-2 text-muted" aria-hidden="true"></i><span class="font-14">{{ $t('SetUpProductType') }}</span></a>
                                </li>
                                <li class="nav-item ThirdScreen" @click="SkipTab(3)"><a class="nav-link left-menu" :class="{ 'active': step == 3 }" style="cursor: pointer;"><i class="fa fa-plus pr-2 text-muted" aria-hidden="true"></i><i class="fa fa-check-circle font-18 pr-2 text-success" v-if="IsClientStoragesCopmleted" aria-hidden="true"></i><span class="font-14">{{ $t('AddDeployeItem') }}</span></a>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="border p-3" v-if="step == 3">
                        <dynamic-form v-if="DataLoaded" ref="modalAddCustomForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-UnitPrice="{ data }">

                                <div class="input-group">
                                    <span class="input-group-append">
                                        <span class="input-group-text" id="spnOutOF">(USD)</span>
                                    </span>
                                    <input type="Number" :maxlength="5" step="any" class="form-control" v-on:keyup="GetAssetCost($event, data.fieldInfo.value, 'UnitPrice')" v-model="data.fieldInfo.value">

                                </div>

                            </template>

                            <template v-slot:tgslot-TotalCost="{ data }">

                                <div class="input-group">
                                    <span class="input-group-append">
                                        <span class="input-group-text" id="spnOutOF">(USD)</span>
                                    </span>
                                    <input type="number" class="form-control" pattern="\d+\.?\d?(?!\d)" step="any" v-on:keyup="GetUnitPrice(data.fieldInfo.value)" v-model="data.fieldInfo.value">

                                </div>
                            </template>
                        </dynamic-form>
                    </div>
                    <div class="right-content row flex-row-reverse mt-3" v-if="step == 3">
                        <div class="col-lg-12 text-left">
                            <div class="py-2 mandatory">
                                {{ $t('MandatoryString') }}
                            </div>
                        </div>
                    </div>

                    <div class="right-content">
                        <div class="col-md-12 p-0" v-if="popupCondition == 'setup'">
                            <div class="col-md-12 p-0" v-if="step == 1">
                                <div class="tab-content">
                                    <div id="step1" class="tab-pane active show">
                                        <section class="page-action w-100">
                                            <div class="container-fluid p-0">
                                                <div class="col-md-12 p-0s addpopuplocation">
                                                    <AddLocationPopup :popupCondition="popupCondition">
                                                    </AddLocationPopup>
                                                </div>
                                            </div>
                                        </section>
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-12 p-0" v-if="step == 2">
                                <div class="tab-content">
                                    <div id="step2" class="tab-pane active show">
                                        <section class="page-action w-100">
                                            <div class="container-fluid p-0">
                                                <div class="col-md-12 p-0">
                                                    <AddProductType :popupCondition="popupCondition" @RefresComp="RefresComp" :key="refreshComponent">
                                                    </AddProductType>
                                                </div>
                                            </div>
                                        </section>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
    <div class="my-popups" v-if="ShowAddLocation">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('Location') }}
                        </h5>
                        <button v-on:click="CloseLocationPopup()" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>

                    </div>
                    <AddLocationPopup></AddLocationPopup>
                </div>
            </div>
        </div>
    </div>

    <TagNamePopup v-if="IsTagNamePopup" :FromAddProduct="FromAddProduct"></TagNamePopup>
    <div class="my-popups" v-if="ShowAddProductType">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('ProductType') }}
                        </h5>
                        <button v-on:click="ClosePopupProductType()" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>
                    </div>
                    <AddProductType :FromPage="FromPage" @RefresComp="RefresComp" :key="refreshComponent">
                    </AddProductType>
                </div>
            </div>
        </div>
    </div>
</section>
</template>

<script>
import DataService from '../../service/DataService'
import TagNamePopup from '../Modal/TagNamePopup.vue';
import AddLocationPopup from '../Modal/AddLocation.vue'
import AddProductType from '../Modal/AddProductType.vue'
export default {
    props: {
        popupCondition: {
            type: String,
            required: false
        },
        FromPage: {
            type: String,
            required: false
        }
    },
    components: {
        TagNamePopup,
        AddLocationPopup,
        AddProductType
    },
    data() {
        return {
            isLoading: false,
            progressBarColor: '',
            AssetType: [],
            BarcodeType: [],
            userLoggedIn2: false,
            IsDeployment: false,
            IsTagNamePopup: false,
            LocationData: [],
            ShowAddLocation: false,
            ShowAddProductType: false,
            FromAddProduct: false,
            DeploymentType: '',
            IsEnableAssetManagement: false,
            fileUploaded: '',
            tagData: [],
            isCheckDeployItem: '',
            refreshComponent: 0,
            DeployProductValue: false,
            FormSchema: [{
                    layoutType: "double",
                    group_name: 'Product Information',
                    group_display_name: this.$t('ProductInformation'),
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('ProductType'),
                            name: "ProductType",
                            value: "",
                            placeholder: "Select Product Type",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onAddButtonClick: this.OpenAddProductTypePopup,
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ProductName'),
                            name: "ProductName",
                            value: "",
                            placeholder: "",
                            config: {

                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('BarcodeType'),
                            name: "BarcodeType",
                            value: "",
                            placeholder: "Select Barcode Type",
                            config: {
                                options: [],

                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "toggleField",
                            label: this.$t('CreateUniqueNameListing'),
                            name: "CreateUniqueNameListing",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": false
                            },
                        },
                        {
                            astype: "FileField",
                            showLabel: true,
                            label: this.$t('DisplayImage'),
                            name: "DisplayImage",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [{
                                        fileType: 'png',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpeg',
                                        iconClass: 'fa-file-image-o'
                                    }
                                ],
                                onChange: this.setFile,
                                showAddIcon: false,
                                onAddButtonClick: this.AddFileMore,
                                multiple: false
                            },
                        },
                        {
                            astype: "MultiSelectField",
                            label: this.$t('Tags'),
                            name: "Tags",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onAddButtonClick: this.OpenAddTagPopup
                            },
                        },
                    ]
                },
                {
                    layoutType: "Single",
                    group_name: 'Deploy Product',
                    group_display_name: this.$t('DEPLOY_ITEM'),

                    Data: [{
                        astype: "toggleField",
                        label: this.$t('DeployProduct'),
                        name: "DeployProduct",
                        value: "",
                        placeholder: "",
                        config: {
                            onChange: this.HideShowDeployProductFields,
                        }
                    }, ]
                },
                {
                    layoutType: "Single",
                    Data: [{
                        astype: "RadioField",
                        label: this.$t('DeploymentType'),
                        name: "DeploymentType",
                        value: "1",
                        placeholder: "",
                        config: {
                            options: [{
                                value: "1",
                                name: "Product"
                            }, {
                                value: "2",
                                name: "Inventory"
                            }],
                            onChange: this.OnDeplymentType
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    }, ]
                },
                {
                    layoutType: "Single",
                    Data: [{
                        astype: "RadioField",
                        label: this.$t('DeploymentType'),
                        name: "SetupDeploymentType",
                        value: "1",
                        placeholder: "",
                        config: {
                            options: [{
                                value: "1",
                                name: "Asset"
                            }],
                            onChange: this.OnDeplymentType
                        },
                        visibility: true,
                        validationRules: {
                            "required": false
                        },
                    }, ]
                },
                {
                    layoutType: "double",
                    Data: [{
                            astype: "TreeSelectField",
                            label: this.$t('Location'),
                            name: "Location",
                            value: null,
                            placeholder: "Select Location",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onAddButtonClick: this.AddLocation
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('Quantity'),
                            name: "Quantity",
                            value: "0",
                            placeholder: "",
                            visibility: true,
                            config: {
                                onKeyUp: this.GetAssetCost
                            },
                            validationRules: {
                                "required": true,
                                max: 7,
                                "min_value": 1
                            },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('UnitPrice'),
                            name: "UnitPrice",
                            value: "0",
                            placeholder: "",
                            validationRules: {
                                "required": false,
                                max: 5,
                                "min_value": 0
                            },
                            visibility: true,
                            config: {
                                onKeyUp: this.GetAssetCost
                            },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('TotalCost'),
                            name: "TotalCost",
                            value: "0",
                            placeholder: "",
                            visibility: true,
                            config: {
                                onKeyUp: this.GetUnitPrice

                            },
                        },
                    ]
                },

            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-secondary",
                    text: "<i class='fa fa-angle-double-left pr-2'></i>Back",
                    onClick: this.BackButton,
                    isVisible: false,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save And Close",
                    name: "SaveAndClose",
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save And New",
                    name: "SaveAndNew",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.close,
                    isVisible: true,
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-check' aria-hidden='true'></i> Finish",
                    name: "Finish",
                    isVisible: false,
                }
            ],
            DataLoaded: 0,
            TagsArray: [],
            tags: [],
            file: [],
            CurrentTab: 1,
            step: 3,
            SetupProgress: 0,
            count: 0,
            PageSize: 10,
            PageNumber: 1,
            SortBy: '',
            SortExp: '',
            IsClientStoragesCopmleted: false,
            UserType: '',
            FromPage: ''
        }
    },
    async created() {
        var vm = this;
        await vm.EnableAssetManagement();
        await vm.GetAssetType();
        await vm.GetBarcodeType();
        await vm.GetLocationByCompanyId();
        await vm.GetTagsByModuleI();
        await vm.AdvancedSetupFields();
        await vm.AssetCatalogueListing();
        await vm.GetProgress();
        vm.HideShowDeployProductFields('false');
        vm.UserType = this.GetUserInfo.UserType;
        if (vm.UserType == 'NU' || vm.UserType == 'PM') {
            (vm.FormSchema)[0].Data[0].config.showAddIcon = false;
            (vm.FormSchema)[4].Data[0].config.showAddIcon = false;
        } else {
            (vm.FormSchema)[0].Data[0].config.showAddIcon = true;
            (vm.FormSchema)[4].Data[0].config.showAddIcon = true;
        }
        vm.DataLoaded = 1;
    },
    mounted() {
        $('#modelAddProduct').modal({
            backdrop: 'static',
            keyboard: false
        })
    },
    methods: {
        RefresComp: function () {
            var vm = this;
            vm.refreshComponent += 1;
        },
        OnDeplymentType(event, field) {
            field.value = event.target.getAttribute('data-value');
            console.log(event.target.getAttribute('data-value'))
        },
        async AssetCatalogueListing() {
            var vm = this;
            vm.isLoading = true;
            var prams = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&${vm.searchCondition}`;
            await DataService.AssetCatalogueListing(prams).then(response => {
                if (response.data.length > 0) {
                    vm.count = vm.count + 1;
                    vm.SetupProgresText = 'Completed';
                } else {
                    vm.SetupProgress = 0;
                }
                vm.isLoading = false;
            })
        },
        GetProgress() {
            var vm = this;
            if (vm.count == 1) {
                vm.SetupProgress = 33.33;
            } else if (vm.count == 2) {
                vm.SetupProgress = 66.66;
            } else if (vm.count == 3) {
                vm.SetupProgress = 100;
            }
        },
        BackButton() {
            var vm = this;
            vm.SkipTab(2);
        },
        SkipTab: function (s) {
            var vm = this;
            vm.step = s;
            vm.CurrentTab = s;
        },
        AdvancedSetupFields() {
            var vm = this;
            if (vm.popupCondition == 'setup') {
                vm.FormSchema[0].Data[5].visibility = false;
                vm.FormSchema[2].Data[0].visibility = false;
                vm.step = 1;
                vm.buttons[1].isVisible = false;
                vm.buttons[0].isVisible = true;
                vm.buttons[3].isVisible = false;
                vm.buttons[4].isVisible = true;
            }
        },
        async GetTagsByModuleI() {
            var vm = this;
            vm.FormSchema[0].Data[5].config.options = [];
            await DataService.GetTagsByModuleId('moduleName=Asset').then(function (response) {
                vm.TagsArray = JSON.parse(response.data).data;
                vm.TagsArray.forEach((itm, index) => {
                    vm.FormSchema[0].Data[5].config.options.push({
                        name: itm.TAG_NAME,
                        value: itm.TAG_ID
                    })
                })
            })
        },

        HideShowDeployProductFields(ev) {
            var vm = this;
            if (ev == true) {
                if (vm.popupCondition == 'setup') {
                    (vm.FormSchema)[2].Data[0].visibility = false;
                    (vm.FormSchema)[3].Data[0].visibility = true;
                } else {
                    (vm.FormSchema)[2].Data[0].visibility = true;
                    (vm.FormSchema)[3].Data[0].visibility = false;
                }
                (vm.FormSchema)[4].Data[0].visibility = true;
                (vm.FormSchema)[4].Data[1].visibility = true;
                (vm.FormSchema)[4].Data[2].visibility = true;
                (vm.FormSchema)[4].Data[3].visibility = true;
            } else {

                if (vm.popupCondition == 'setup') {
                    (vm.FormSchema)[2].Data[0].visibility = false;
                    (vm.FormSchema)[3].Data[0].visibility = false;
                } else {
                    (vm.FormSchema)[2].Data[0].visibility = false;
                    (vm.FormSchema)[3].Data[0].visibility = false;
                }
                (vm.FormSchema)[4].Data[0].visibility = false;
                (vm.FormSchema)[4].Data[1].visibility = false;
                (vm.FormSchema)[4].Data[2].visibility = false;
                (vm.FormSchema)[4].Data[3].visibility = false;
            }
        },
        onSubmit: function (formfields, val) {
            var vm = this;
            vm.buttons[1].class += " disabled";
            vm.isLoading = true
            if (vm.IsEnableAssetManagement) {
                // if(formfields.DeployProduct == true && formfields.Location=='')
                // {
                //     vm.ShowAlert(vm.$t("Please select Location!"), "failure",vm.$t('Alert'));
                // }

                let myObj = {};
                if (formfields.DeployProduct == true) {
                    myObj.DeployProduct = formfields.DeployProduct;
                    myObj.DeployId = '0';
                    myObj.LocationId = formfields.Location;
                    myObj.Quantity = parseInt(formfields.Quantity);
                    myObj.UnitPrice = parseFloat(formfields.UnitPrice);
                    myObj.DeploymentType = formfields.DeploymentType == "" ? formfields.SetupDeploymentType : formfields.DeploymentType;
                } else {
                    myObj.DeployProduct = formfields.DeployProduct == '' ? false : true;
                    myObj.DeployId = "0";
                    myObj.LocationId = "-1";
                    myObj.Quantity = "0";
                    myObj.UnitPrice = "0";
                }
                myObj.AssetCatalogueId = "0";
                myObj.Name = formfields.ProductName,
                    myObj.AssetTypeId = formfields.ProductType,
                    myObj.BarcodeTypeId = formfields.BarcodeType;
                myObj.StatusId = "1001",
                    myObj.TotalCost = formfields.TotalCost;
                myObj.AssetCost = formfields.TotalCost;
                myObj.ParentUniqueId = "",
                    myObj.ChildUniqueId = "",
                    myObj.ChildCatalogueId = "",
                    myObj.CatalogueId = "",
                    myObj.CatalogHdnId = "",
                    myObj.SelfieEnabled = "",
                    myObj.ChildCategoryName = "",
                    myObj.AssetUniqueName = "",
                    myObj.AssetCatalogName = "",
                    myObj.Description = "",
                    myObj.AssetTagIdsWizard = Array.prototype.map.call(formfields.Tags, function (item) {
                        return item.value;
                    }).join(",");
                myObj.AssetTypeTagIdsWizard = "",
                    myObj.Manufacturer = "",
                    myObj.NumberOfItems = formfields.Quantity,
                    myObj.AsseetGroupQuantity = "",
                    myObj.ItemCode = "",
                    myObj.ChildItemCode = "",
                    myObj.ChildCatalogName = "",
                    myObj.ChildRequestQuantity = "",
                    myObj.ChildAssetType = "",
                    myObj.AssetType = "",
                    myObj.StandardTypeId = "",
                    myObj.AssetCompartmentId = "",
                    myObj.AssetGroupId = "",
                    myObj.IsSelfCheckout = "",
                    myObj.IsEnableTerms = "",
                    myObj.SubLocationId = "",
                    myObj.Attachment = "",
                    myObj.CreatedAt = "",
                    myObj.CreatedBy = "",
                    myObj.ModifiedAt = "",
                    myObj.ModifiedBy = "",
                    myObj.Asset_Type = "",
                    myObj.Status = "",
                    myObj.ForCompany = "",
                    myObj.ForEmployee = "",
                    myObj.MoveToInTransitInfo = "",
                    myObj.AvailableItems = formfields.Quantity,
                    myObj.PendingAvailableQuantity = "",
                    myObj.PendingTotalAvailableItems = "",
                    myObj.TotalQuantity = "",
                    myObj.ChildAvailableItems = "",
                    myObj.TotalFilesCount = "",
                    myObj.AssetTypeCategory = "",
                    myObj.AssetTypeCategoryName = "",
                    myObj.AssetTypeCategoryCode = "",
                    myObj.AttachmentName = "",
                    myObj.DisplayImage = "",
                    myObj.IsTermsAndCondition = "",
                    myObj.TotalTermsConditionFilesCount = "",
                    myObj.RelatedInfoId = "",
                    myObj.UniqueName = formfields.CreateUniqueNameListing == true ? 1 : 0,
                    myObj.AssetCatalogueGroup = "",
                    myObj.ChildCreateUniqueNameListing = "",
                    myObj.IsTransitItems = "",
                    myObj.AttachmentPath = "",
                    myObj.LocationName = "",
                    myObj.SubLocationName = "",
                    myObj.AuditFrequency = "",
                    myObj.ImageCaptureFrequency = "",
                    myObj.CalibrationFrequency = "",
                    myObj.CR = "",
                    myObj.DR = "",
                    myObj.IsDisplayImage = "",
                    myObj.PurchaseValue = "",
                    myObj.CurrentBookValue = "",
                    myObj.SalvageValue = "",
                    myObj.Unit = "",
                    myObj.ReqFrom = "",
                    myObj.IsContainer = "",
                    myObj.IsSampleData = "",
                    myObj.Model = "",
                    myObj.Vendor = "",
                    myObj.ManufacturerId = "",
                    myObj.PurchaseOrder = "",
                    myObj.WarrantyDuration = "",
                    myObj.DepreciationRuleId = "",
                    myObj.OrderDate = "",
                    myObj.SalvageCost = "",
                    myObj.ItemLife = "",
                    myObj.InvoiceNumber = "",
                    myObj.InvoiceDate = "",
                    myObj.InsurerName = "",
                    myObj.InsuranceRefNumber = "",
                    myObj.InsuranceTo = "",
                    myObj.DeployedBy = "",
                    myObj.DeployedAt = "",
                    myObj.EnableWarranty = "",
                    myObj.EnableInsurance = "",
                    myObj.EnableItemCost = "",
                    myObj.EnableCalibration = "",
                    myObj.EnableDepreciation = "";

                var formData = new FormData();
                var postString = {
                    "postString": myObj
                }
                formData.append("postString", JSON.stringify(myObj));
                formData.append("Files", formfields.DisplayImage[0]);
                formData.append("Is_DisplayImage", 'Yes');
                DataService.AddAssetWizard(formData).then(function (response) {
                    if (val == 'SaveAndClose') {
                        vm.$parent.ShowAddProduct = false;
                        vm.$parent.FetchData();
                        if (response.data.msg == "size_exceed" || response.data.msg == "FileSizeExceeded" || response.data.msg == "ExceedAllowedSize") {
                            vm.ShowAlert((vm.$t('size_exceed', [vm.$t('Asset')]), "warning", true, vm.$t("Alert")))
                        } else if (response.data.msg == 'alreadyexist') {
                            vm.ShowAlert(vm.$t("Already exist record!"), "warning", vm.$t('Alert'));
                        } else if (response.data.msg == 'ValidFileUploadMsg') {
                            vm.ShowAlert(vm.$t("Please upload valid file!"), "warning", vm.$t('Alert'));
                        } else if (response.data.msg == 'add') {
                            vm.ShowAlert(vm.$t("Product has been Saved Successfully"), "success", vm.$t('Alert'));
                            vm.$parent.ShowAddProduct = false;
                            if (vm.popupCondition == 'setup') {
                                $("#modelAddProduct").modal("hide");
                                $("body").removeClass("modal-open");
                                $(".modal-backdrop").remove();
                            } else {
                                vm.$parent.FetchData();
                            }
                            vm.isLoading = false;
                        } else {
                            vm.ShowAlert(vm.$t("Unknown Error"), "failure", vm.$t('Alert'));
                            vm.$parent.ShowAddProduct = false;
                            if (vm.popupCondition == 'setup') {
                                $("#modelAddProduct").modal("hide");
                                $("body").removeClass("modal-open");
                                $(".modal-backdrop").remove();
                            } else {
                                vm.$parent.FetchData();
                            }
                            vm.isLoading = false;
                        }
                    } else if (val == 'SaveAndNew') {
                        vm.buttons[2].class += " disabled";
                        if (response.data.msg == "size_exceed" || response.data.msg == "FileSizeExceeded" || response.data.msg == "ExceedAllowedSize") {
                            vm.ShowAlert((vm.$t('size_exceed', [vm.$t('Asset')]), "warning", true, vm.$t("Alert")))
                        } else if (response.data.msg == 'alreadyexist') {
                            vm.ShowAlert(vm.$t("Already exist record!"), "warning", vm.$t('Alert'));
                            vm.buttons[2].class = vm.buttons[2].class.replace("disabled", "").trim();
                        } else if (response.data.msg == 'ValidFileUploadMsg') {
                            vm.ShowAlert(vm.$t("Please upload valid file!"), "warning", vm.$t('Alert'));
                        } else if (response.data.msg == 'add') {

                            if (vm.popupCondition == 'setup') {
                                vm.$parent.ShowAddProduct = false;
                                vm.ShowAlert(vm.$t("Product has been Saved Successfully"), "success", vm.$t('Alert'));
                                vm.resetForm();
                                vm.buttons[2].class = vm.buttons[2].class.replace("disabled", "").trim();
                            } else {
                                vm.$parent.ShowAddProduct = false;
                                vm.ShowAlert(vm.$t("Product has been Saved Successfully"), "success", vm.$t('Alert'));
                                if (vm.FromPage == 'RelatedInfo') {
                                    vm.$parent.ShowAddProduct = false; // Close the current popup
                                    vm.$nextTick(() => {
                                        vm.$parent.ShowAddProduct = true; // Open the popup after the next DOM update
                                        vm.$parent.OpenModelAddProduct();
                                        vm.HideShowDeployProductFields('false');
                                    });
                                } else if (vm.FromPage == 'ProductListing') {
                                    vm.HideShowDeployProductFields('false');
                                    vm.$parent.OpenModelAddProduct();
                                }
                                vm.isLoading = false;
                            }
                        } else {
                            vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                            vm.$parent.ShowAddProduct = false;
                            if (vm.popupCondition == 'setup') {
                                vm.$parent.ShowAddProduct = false;
                                vm.resetForm();
                                vm.buttons[2].class = vm.buttons[2].class.replace("disabled", "").trim();

                            } else {
                                vm.$parent.FetchData();
                            }
                            vm.isLoading = false;
                        }
                        vm.$refs.modalAddCustomForm.validateForm.reset();
                    } else if (val == 'Finish') {
                        if (response.data.msg == "size_exceed" || response.data.msg == "FileSizeExceeded" || response.data.msg == "ExceedAllowedSize") {
                            vm.ShowAlert((vm.$t('size_exceed', [vm.$t('Asset')]), "warning", true, vm.$t("Alert")))
                        } else if (response.data.msg == 'alreadyexist') {
                            vm.ShowAlert(vm.$t("Already exist record!"), "warning", vm.$t('Alert'));
                            vm.buttons[2].class = vm.buttons[2].class.replace("disabled", "").trim();
                        } else if (response.data.msg == 'ValidFileUploadMsg') {
                            vm.ShowAlert(vm.$t("Please upload valid file!"), "warning", vm.$t('Alert'));
                        } else if (response.data.msg == 'add') {

                            if (vm.popupCondition == 'setup') {
                                vm.$parent.ShowAddProduct = false;
                                vm.ShowAlert(vm.$t("Product has been Saved Successfully"), "success", vm.$t('Alert'));
                                vm.close();
                            }
                        } else {
                            vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                            vm.$parent.ShowAddProduct = false;
                            if (vm.popupCondition == 'setup') {
                                vm.$parent.ShowAddProduct = false;
                            } else {
                                vm.$parent.FetchData();
                            }
                            vm.isLoading = false;
                        }
                          vm.$refs.modalAddCustomForm.validateForm.reset();
                    }
                    vm.$refs.modalAddCustomForm.validateForm.reset();
                    vm.isLoading = false;
                })
            } else {
                vm.isLoading = false;
                vm.ShowAlert(vm.$t("Unauthorized Access"), "warning", vm.$t('Alert'));
            }
            vm.isLoading = false;
        },
       
        resetForm() {
            this.FormSchema.forEach((group) => {
                group.Data.forEach((field) => {
                    if (field.astype === 'SelectField' || field.astype === 'RadioField') {
                        field.value = '';
                    } else if (field.astype === 'TextField') {
                        field.value = '';
                    } else if (field.astype === 'NumericField') {
                        field.value = '0';
                    } else if (field.astype === 'MultiSelectField') {
                        field.value = [];
                    } else if (field.astype === 'TreeSelectField') {
                        field.value = null;
                    } else if (field.astype === 'ToggleField') {
                        field.value = false;
                    } else if (field.astype === 'FileField') {
                        field.value = '';
                    } else if (field.astype === 'SlotField') {
                        field.value = '';
                    }
                });
            });
        },

        setFile: function (e) {
            var vm = this;
            for (let index = 0; index < e.target.files.length; index++) {
                const element = e.target.files[index];
                vm.file.push(element)
            }
        },
        isDeployProduct() {
            var vm = this;
            vm.FormSchema[3].Data[0].isVisible = true;
        },

        GetAssetCost(event, value, val) {
            var vm = this;
            if (event.key === 'e') {
                event.preventDefault();
            }
            var quantityCheck = (vm.FormSchema)[4].Data[1].value;
            var quantityField = (vm.FormSchema)[4].Data[1];

            if (quantityCheck.length > 7) {
                quantityCheck = quantityCheck.slice(0, 7);
            }
            quantityField.value = quantityCheck;

            var unitPrice = (vm.FormSchema)[4].Data[2].value;
            var unitPriceField = (vm.FormSchema)[4].Data[2];
            if (unitPrice.length > 5) {
                unitPrice = unitPrice.slice(0, 5);
                unitPriceField.value = unitPrice;
                vm.ShowAlert(vm.$t("The Unit Price field may not be greater than 5 characters"), "failure", vm.$t('Alert'));
            }
            vm.GetUnitPrice(quantityCheck);
        },
        GetUnitPrice(val) {
            var vm = this;
            var quantity = (vm.FormSchema)[4].Data[1].value;
            if (quantity.startsWith('0') && quantity.length > 1) {
                quantity = quantity.slice(1);
            }
            var unitPrice = (vm.FormSchema)[4].Data[2].value;
            if (unitPrice.startsWith('0') && unitPrice.length > 1) {
                unitPrice = unitPrice.slice(1);
            }
            if (quantity.length > 0 && !quantity.startsWith('0')) {
                if (unitPrice.length > 0 && !unitPrice.startsWith('0')) {
                    var assetCost = parseFloat(unitPrice) * parseFloat(quantity);
                    (vm.FormSchema)[4].Data[3].value = assetCost.toFixed(3);
                } else {
                    (vm.FormSchema)[4].Data[3].value = "0.000";
                }
            } else if (unitPrice.length > 0 && !unitPrice.startsWith('0')) {
                var unitPriceFloat = parseFloat(unitPrice);
                (vm.FormSchema)[4].Data[3].value = unitPriceFloat.toFixed(3);
            } else {
                // If both quantity and unitPrice are empty or start with '0', set assetCost to 0
                (vm.FormSchema)[4].Data[3].value = "0.000";
            }
        },
        UploadFile(event) {

            var vm = this;
            vm.fileUploaded = event.target.files[0];
        },
        removeTag: function (tagId) {
            var vm = this;
            vm.confirmR(vm.$t('ConfirmRemoveTag'), true, vm.$t("Delete"),
                function () {
                    var tagIndex = vm.tagData.findIndex(x => x.TAG_ID === tagId);
                    if (tagIndex > -1) {
                        vm.tagData.splice(tagIndex, 1);
                    }
                });
        },
        AddLocation() {
            var vm = this;
            vm.IsDeployment = true;
            vm.ShowAddLocation = true;
        },
        async GetLocationByCompanyId() {
            var vm = this;
            vm.isLoading = true;
            vm.LocationData = [];
            var params = `reqForm=`
            await DataService.GetLocationByCompanyId(params)
                .then(function (response) {
                    vm.FindalLocationArray = [];
                    vm.LocationData = JSON.parse(response.data).data;
                    //var json =vm.MakeNormalArray(response.data).data;
                    vm.LocationData.forEach((value) => {
                        var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                            return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                        });
                        if (CheckIfExist.length == 0) {
                            if (value.PARENT_LOCATION_ID == null) {
                                let locationId = value.COMPANY_LOCATION_ID
                                let rslt = vm.getChildLocation(locationId, value, JSON.parse(response.data).data)
                                vm.FindalLocationArray.push(rslt)
                            }
                        }
                    });
                    (vm.FormSchema)[4].Data[0].config.options = vm.FindalLocationArray;
                });

            if (vm.LocationData.length > 0) {
                vm.count = vm.count + 1;
                vm.SetupProgresText = 'Completed';
            } else {
                vm.SetupProgress = 0;
            }
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        OpenAddTagPopup() {
            var vm = this;
            vm.ShowAddProduct = true;
            $("#modelAddProduct").modal('show');
            setTimeout(function () {
                $("#modelAddProduct").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 200);
            }, 1000);
            vm.FromAddProduct = true
            vm.IsTagNamePopup = true;
        },
        async GetBarcodeType() {
            var vm = this;
            vm.isLoading = true;
            var url = `BarcodeTypeId=`
            await DataService.GetBarcodetype(url).then(response => {
                vm.BarcodeType = JSON.parse(response.data.result);
                vm.BarcodeType.forEach((itm, index) => {
                    (vm.FormSchema)[0].Data[2].config.options.push({
                        name: itm.BARCODE_TYPE,
                        value: itm.BARCODE_TYPE_ID
                    })
                })
                vm.isLoading = false;
            })
        },
        GetAssetType() {
            var vm = this;
            (vm.FormSchema)[0].Data[0].config.options = [];
            vm.isLoading = true;
            var url = `type=ITEMCODE&issueTo=&locationId=`;
            DataService.GetAssetTypeCustom(url).then(response => {
                vm.AssetType = JSON.parse(response.data);
                vm.AssetType.forEach((itm, index) => {
                    (vm.FormSchema)[0].Data[0].config.options.push({
                        name: itm.ASSETTYPE,
                        value: itm.ASSETID
                    })
                })
                vm.isLoading = false;
                vm.$refs.modalAddCustomForm.UpdateKeyValue();
            })
            if (vm.AssetType.length > 0) {
                vm.count = vm.count + 1;
                vm.SetupProgresText = 'Completed';
            } else {
                vm.SetupProgress = 0;
            }
        },
        close() {
            var vm = this;
            if (vm.popupCondition == 'setup') {
                vm.$parent.showpopup = false;
            } else {
                $("#modelAddProduct").modal("hide");
                $("body").removeClass("modal-open");
                $(".modal-backdrop").remove();
                vm.$parent.FetchData();
                vm.$parent.ShowAddProduct = false;
            }
        },
        CloseTagPopup() {
            var vm = this;
            vm.IsTagNamePopup = false;
        },
        async CloseLocationPopup() {
            var vm = this;
            vm.LocationData = [];
            vm.FindalLocationArray = [];
            (vm.FormSchema)[4].Data[0].config.options = [];
            await vm.GetLocationByCompanyId();
            vm.HideShowDeployProductFields(false);
            setTimeout(function () {
                vm.HideShowDeployProductFields(true);
            }, 500);
            vm.isLoading = false;
            vm.ShowAddLocation = false;

        },
        async EnableAssetManagement() {
            let url = `moduleName=Asset&configkey=CanSeeAssetManagement`;
            await DataService.EnableAssetManagement(url).then(response => {
                this.IsEnableAssetManagement = response.data;

            });
        },
        OpenAddProductTypePopup() {
            var vm = this;
            vm.FromPage = 'AddProduct'
            vm.ShowAddProductType = true;
        },
        ClosePopupProductType() {
            var vm = this;
            vm.ShowAddProductType = false;
        },
    }
}
</script>

<style>
