    white-space: normal;
    font-size: 80%;
    position: absolute !important;
    margin-top: 7% !important;
    padding-bottom: 10% !important;
}

#Location .vue-treeselect__control .vue-treeselect__x-container {
    display: none;
}

#Location .vue-treeselect {
    position: relative;
    text-align: left;
